.block-contact {
    h3 {
        margin: 0;
    }
    .contact__title,
    li {
        font-size: 2.5em;
        line-height: 1.6;

        @media all and (max-width: $breakpoint_laptop) {
            font-size: 1.875em;
            line-height: 1.4;
        }
        @media all and (max-width: $breakpoint_tablet-portrait - 1px) {
            font-size: 1.25em;
        }
    }
}
